.price {

    &__header {
        display: flex;
        flex-direction: column;
        max-width: 200px;
        text-align: center;
        align-items: center;
        margin: 72px auto;
    }

    &__item {
        display: flex;
        align-items: center;
        max-width: 550px;
        height: 215px;
        background: #FFE7E7;

        margin-top: 50px;
    }

    &__item-img {
        max-width: 123px;
        height: 123px;
        margin-left: 40px;
    }

    &__item-text {
        margin-left: 84px;
        font-family: 'Times New Roman', Times, serif;
        font-size: 26px;
        line-height: 37px;
        height: 135px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    &__item-name {
        width: 280px;
    }

    &__container {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: stretch;
    }

    @include _768 {
        &__item-name {
            width: 170px;
            font-size: 20px;
        }

        &__item-img {
            max-width: 86px;
            height: 86px;
            margin-left: 28px;
        }
        &__item-text{
            margin-left: 40px;
            font-size: 18px;
        }
    }

    
}